@import url("https://fonts.googleapis.com/css2?family=Bree+Serif&family=Caveat:wght@500&display=swap");
*
{
  margin:0;
  padding:0;
  font-family: "Verdana", cursive, sans-serif;
  color: #ffffff;
}
h1{
  text-align: center;
  color: #f71aa2;
}
body{
  background-image: url('http://superdevresources.com/wp-content/uploads/2014/02/pattern-background.png');
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  width:100%;
}
/* //div */
.app
{
background: linear-gradient(180deg,#f3bd0d 0%, #ff8100 80%);
width: 100%;
  min-height: 215px;
  height: min-content;
  border-radius: 13px;
  padding: 25px;
  box-shadow: 10px 10px 42px 0px rgba(0, 0, 0, 0.75);
  display: flex;
  justify-content: space-evenly;
}
/* you scored 3 out of 4 */
.score-section{
  display: flex;
  font-size: 24px;
  align-items: center;
}
/* left section */
.question-section{
  position:relative;
  width:100%;
}
.question-count {
  margin-bottom: 20px;
}

.question-count span {
  font-size: 28px;
}

.question-text {
  margin-bottom: 12px;
  color:rgb(0,0,0,0.9);
}
/* right section */
.answer-section {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
button {
  width: 100%;
  font-size: 16px;
  color: #ffffff;
  margin-left: 5px;
  background: linear-gradient(180deg,#cd0df3 0%, #ff8100 80%);
  border-radius: 5px;
  display: flex;
  padding: 6px;
  justify-content: flex-start;
  align-items: center;
  border: 2px solid #234668;
  cursor: pointer;
}


button:hover {
  background-color: #a8a8ab;
}

button:focus {
  outline: none;
}